import logo from './logo.svg';
import './App.css';
import BasicTable from './components/Table/Table';
import BookTable from './components/BookTable/BookTable';
import ButtonsGroup from './components/Buttons/Buttons';
import React, { useState } from 'react';
import OutlinedButtons from './components/SortButton/SortButton'


function App() {

  const [viewMode, setViewMode] = useState(0);

  

  if (viewMode === 0) {
    return (
      <div className="App">
        
        <div className = "buttonsWrapper">
          <ButtonsGroup setViewMode = {setViewMode}/>
        </div>
        <h1 className = "mainLabel"> Список учащихся</h1>
        <div className = "tableWrapper">
          <BasicTable/>
        </div>
        <OutlinedButtons/>
      </div>
    );
  }
  return (
    <div className="App">
      <div className = "buttonsWrapper">
        <ButtonsGroup setViewMode = {setViewMode}/>
      </div>
      <h1 className = "mainLabel"> Список книг</h1>
      <div className = "tableWrapper">
        <BookTable/>
      </div>
      <OutlinedButtons/>
    </div>
  );

}

export default App;
