const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});

function createData(name, born, addmission, year, group, grade2018, grade2019, grade2020) {
  return { name, born, addmission, year, group, grade2018, grade2019, grade2020 };
}

const rows = [
  createData(Students[0].name,Students[0].born, Students[0].addmission, Students[0].year, Students[0].group, Students[0].grade2018, Students[0].grade2019, Students[0].grade2020),
  createData(Students[1].name,Students[1].born, Students[1].addmission, Students[1].year, Students[1].group, Students[1].grade2018, Students[1].grade2019, Students[1].grade2020),
  createData(Students[2].name,Students[2].born, Students[2].addmission, Students[2].year, Students[2].group, Students[2].grade2018, Students[2].grade2019, Students[2].grade2020),
  createData(Students[3].name,Students[3].born, Students[3].addmission, Students[3].year, Students[3].group, Students[3].grade2018, Students[3].grade2019, Students[3].grade2020),
  createData(Students[3].name,Students[4].born, Students[4].addmission, Students[4].year, Students[4].group, Students[4].grade2018, Students[4].grade2019, Students[4].grade2020),
];

const BasicTable = () => {
  const classes = useStyles();

  return (
    <TableContainer component={Paper}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>Имя и Фамилия</TableCell>
            <TableCell align="right">Дата рождения</TableCell>
            <TableCell align="right">Год поступления</TableCell>
            <TableCell align="right">Курс</TableCell>
            <TableCell align="right">Группа</TableCell>
            <TableCell align="right">Средняя оценка за : 2018</TableCell>
            <TableCell align="right">2019</TableCell>
             <TableCell align="right">2020</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow key={row.name}>
              <TableCell component="th" scope="row">
                {row.name}
              </TableCell>
              <TableCell align="right">{row.born}</TableCell>
              <TableCell align="right">{row.addmission}</TableCell>
              <TableCell align="right">{row.year}</TableCell>
              <TableCell align="right">{row.group}</TableCell>
              <TableCell align="right">{row.grade2018}</TableCell>
              <TableCell align="right">{row.grade2019}</TableCell>
              <TableCell align="right">{row.grade2020}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}

export default BasicTable;